import { Component, OnInit } from '@angular/core';
import { Animation, AnimationController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';


@Component({
  selector: 'app-edit-variation',
  templateUrl: './edit-variation.page.html',
  styleUrls: ['./edit-variation.page.scss'],
})
export class EditVariationPage implements OnInit {

  var_id: any;
  var_name: any;
  var_class: any;
  var_price: any;
  var_stock: any;

  vclass: any="";
  vprice: any="";
  vstock: any="";

  constructor(
    private animatioCntrl: AnimationController,
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService
  ) { }

  ngOnInit() {
    this.var_id=this.navParamService.getVarID();
    this.var_name=this.navParamService.getVarName();
    this.var_class=this.navParamService.getVarClass();
    this.var_price=this.navParamService.getVarPrice();
    this.var_stock=this.navParamService.getVarStock();
    console.log(this.var_stock);
  }

  save(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'edit_variation',
        var_id: this.var_id,
        var_name: this.var_name,
        var_class: this.var_class,
        var_price: this.var_price,
        var_stock: this.var_stock,
        vclass: this.vclass,
        vprice: this.vprice,
        vstock: this.vstock
        
      }
      this.accessProviders.postData(body, 'edit_variation.php').subscribe((res:any) => {
        console.log('here');
        if(res.success == true) {
           console.log(res.msg);
           console.log(res.success);
           this.navController.navigateRoot('/edit-product');
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  
}
